/* General container for both pages */
.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url("../../../public/assets/starry.gif") top center;
  color: #fff;
  font-family: Arial, sans-serif;
}

/* Shared form styling */
.form-container {
  background-color: #7777ed;
  padding: 20px;
  border-radius: 15px;
  width: 400px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.form-container h2 {
  margin-bottom: 20px;
  color: black;
}

.form-container form {
  display: flex;
  flex-direction: column;
}

.form-container label {
  margin: 10px 0 5px;
  text-align: left;
  font-size: 14px;
  color: black;
}

.form-container input {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  font-size: 14px;
}

.form-container input:focus {
  border-color: #7777ed;
  box-shadow: 0 0 5px #7777ed;
}

.form-button {
  background-color: #ffa500;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
}

.form-button:hover {
  background-color: #ff8c00;
}

/* Toggle link button */
.toggle-button {
  background: none;
  border: none;
  color: #140a4e;
  cursor: pointer;
  font-size: 14px;
}

.toggle-button:hover {
  text-decoration: underline;
}

.error-message {
  color: red;
  font-size: 0.85em;
}
